import { useState, useEffect } from 'react';
import { StudentApi } from '../api/student.api';
import { CategoryApi } from '../api/category.api';
import { StatusApi } from '../api/status.api';

export const useReferenceData = () => {
  const [students, setStudents] = useState([]);
  const [categories, setCategories] = useState([]);
  const [statuses, setStatuses] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchAll = async () => {
      try {
        setLoading(true);
        setError(null);
        
        // Загружаем все справочники параллельно для оптимизации
        const [studentsRes, categoriesRes, statusesRes] = await Promise.all([
          StudentApi.getAll(),
          CategoryApi.getAll(),
          StatusApi.getAll()
        ]);

        setStudents(studentsRes.data || []);
        setCategories(categoriesRes.data || []);
        setStatuses(statusesRes.data || []);
        
      } catch (err) {
        const message = err.response?.data?.message || 'Ошибка загрузки справочных данных';
        setError(message);
        console.error('Ошибка загрузки справочников:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchAll();
  }, []); // Пустой массив зависимостей = грузим один раз при монтировании

  // Вспомогательные методы для удобного поиска по ID
  const getStudentById = (id) => {
    return students.find(s => s.id === id) || null;
  };

  const getCategoryById = (id) => {
    return categories.find(c => c.id === id) || null;
  };

  const getStatusById = (id) => {
    return statuses.find(s => s.id === id) || null;
  };

  // Поиск по slug (для статусов)
  const getStatusBySlug = (slug) => {
    return statuses.find(s => s.slug === slug) || null;
  };

  // Метод для принудительного обновления данных
  const refresh = async () => {
    setLoading(true);
    try {
      const [studentsRes, categoriesRes, statusesRes] = await Promise.all([
        StudentApi.getAll(),
        CategoryApi.getAll(),
        StatusApi.getAll()
      ]);

      setStudents(studentsRes.data || []);
      setCategories(categoriesRes.data || []);
      setStatuses(statusesRes.data || []);
      setError(null);
    } catch (err) {
      const message = err.response?.data?.message || 'Ошибка обновления справочников';
      setError(message);
    } finally {
      setLoading(false);
    }
  };

  return {
    // Данные
    students,
    categories,
    statuses,
    
    // Состояния
    loading,
    error,
    
    // Методы для поиска
    getStudentById,
    getCategoryById,
    getStatusById,
    getStatusBySlug,
    
    // Метод для обновления
    refresh
  };
};